import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useCookies } from 'react-cookie';
import { useDaumPostcodePopup, Address } from 'react-daum-postcode';

import { useUserStore } from 'src/stores';
import { SignInRequestDto, SignUpRequestDto } from 'src/interfaces/request/auth';
import InputBox from 'src/components/InputBox';
import { signInMock, userMock } from 'src/mocks';
import { INPUT_ICON, MAIN_PATH, emailPattern, telNumberPattern } from 'src/constants';
import { getSignInUserRequest, signInRequest, signUpRequest } from 'src/apis';

import './style.css';
import { SignInResponseDto } from 'src/interfaces/response/auth';
import ResponseDto from 'src/interfaces/response/response.dto';
import { GetLoginUserResponseDto } from 'src/interfaces/response/user';

//          component          //
// description:   //
export default function Authentication() {

  //          state          //
  // description: Cookie  //
  const [cookies, setCookie] = useCookies();

  // description:      //
  const [view, setView] = useState<'sign-in' | 'sign-up'>('sign-in');

  //          function          //
  // description:      //
  const navigator = useNavigate();

  //          event handler          //
  
  //          component          //
  // description:    //
  const SignInCard = () => {

    //          state          //
    // description:     //
    const { setUser } = useUserStore();

    // description:     //
    const [showPassword, setShowPassword] = useState<boolean>(false);
    // description:    //
    const [error, setError] = useState<boolean>(false);
    // description:     //
    const [email, setEmail] = useState<string>(signInMock.email);
    // description:     //
    const [password, setPassword] = useState<string>(signInMock.password);

    //          function          //
    const signInResponseHandler = (result: SignInResponseDto | ResponseDto) => {
      const { code } = result;
      if (code === 'DM') setError(true);
      if (code === 'DE') alert(' .');
      if (code !== 'SU') return;

      const { token, expiredTime } = result as SignInResponseDto;

      const now = new Date().getTime();
      const expires = new Date(now + expiredTime * 1000);

      setCookie('accessToken', token, { expires, path: MAIN_PATH });
      navigator(MAIN_PATH);
    }

    //          event handler          //
    // description:       //
    const onPasswordIconClickHandler = () => {
      setShowPassword(!showPassword);
    }
    // description:     //
    const onSignUpClickHandler = () => {
      setView('sign-up');
    }
    // description:     //
    const onSignInButtonClickHandler = async () => {

      const data: SignInRequestDto = {
        email,
        password
      }
      
      signInRequest(data).then(signInResponseHandler);
    }

    //          component          //

    //          effect          //

    //          render          //
    return (
      <div className='auth-card'>
        <div className='auth-card-top'>
          <div className='auth-card-top-text-container'>
            <div className='auth-card-top-text'></div>
          </div>
          <div className='auth-card-top-input-container'>
            <InputBox label=' ' type='text' placeholder='   .' error={error} value={email} setValue={setEmail} />
            <InputBox label='' type={showPassword ? 'text' : 'password'} placeholder='  .' icon={showPassword ? INPUT_ICON.ON : INPUT_ICON.OFF} buttonHandler={onPasswordIconClickHandler} error={error} value={password} setValue={setPassword} />
          </div>
        </div>
        <div className='auth-card-bottom'>
          {error && (
            <div className='auth-card-bottom-error-message'>
              {'     .\n   .'}
            </div>
          )}
          <div className='auth-card-bottom-button' onClick={onSignInButtonClickHandler}></div>
          <div className='auth-card-bottom-text'>
             ? <span className='auth-emphasis' onClick={onSignUpClickHandler}></span>
          </div>
        </div>
      </div>
    )
  }

  //          component          //
  // description:    //
  const SignUpCard = () => {

    //          state          //
    // description:   ()   //
    const open = useDaumPostcodePopup();
    // description:     //
    const [page, setPage] = useState<1 | 2>(1);
    // description:     //
    const [showPassword, setShowPassword] = useState<boolean>(false);
    // description:      //
    const [showPasswordCheck, setShowPassowrdCheck] = useState<boolean>(false);
    // description:     //
    const [emailPatternError, setEmailPatternError] = useState<boolean>(false);
    // description:     //
    const [emailDuplicationError, setEmailDuplicationError] = useState<boolean>(false);
    // description:     //
    const [passwordError, setPasswordError] = useState<boolean>(false);
    // description:     //
    const [passwordCheckError, setPasswordCheckError] = useState<boolean>(false);
    // description:    //
    const [nicknameError, setNicknameError] = useState<boolean>(false);
    //description:     //
    const [nicknameDuplicationError, setNicknameDuplicationError] = useState<boolean>(false);

    // description:     //
    const [telNumberError, setTelNumberError] = useState<boolean>(false);
    // description:     //
    const [telNumberDuplicationError, setTelNumberDuplicationError] = useState<boolean>(false);

    // description:    //
    const [addressError, setAddressError] = useState<boolean>(false);
    // description:    //
    const [email, setEmail] = useState<string>('');
    // description:    //
    const [password, setPassword] = useState<string>('');
    // description:     //
    const [passwordCheck, setPasswordCheck] = useState<string>('');
    // description:    //
    const [nickname, setNickname] = useState<string>('');
    // description:    //
    const [telNumber, setTelNumber] = useState<string>('');
    // description:    //
    const [address, setAddress] = useState<string>('');
    // description:    //
    const [addressDetail, setAddressDetail] = useState<string>('');

    //          function          //
    // description: 1 2     //
    const checkPage1 = () => {
      const emailPatternFlag = !emailPattern.test(email);
      const passwordFlag = password.length < 8;
      const passwordCheckFlag = password !== passwordCheck;

      setEmailPatternError(emailPatternFlag);
      setPasswordError(passwordFlag);
      setPasswordCheckError(passwordCheckFlag);

      if (!emailPatternFlag && !passwordFlag && !passwordCheckFlag) setPage(2);
    }
    // description: 2     //
    const checkPage2 = () => {
      const telNumberFlag = !telNumberPattern.test(telNumber);

      setTelNumberError(telNumberFlag);
      setNicknameError(!nickname);
      setAddressError(!address);

      // if (!telNumberFlag && nickname && address) setView('sign-in');

      // description:    (  ) //
      const data: SignUpRequestDto = {
        email,
        password,
        nickname,
        telNumber,
        address,
        addressDetail
      }

      signUpRequest(data).then(signUpResponseHandler);
    }

    const signUpResponseHandler = (code: string) => {
      // description: SU :  //
      if (code === 'SU') setView('sign-in');

      // description: EE :   //
      if (code === 'EE') {
        setEmailDuplicationError(true);
        setPage(1);
      }

      // description: EN :   //
      if (code === 'EN') setNicknameDuplicationError(true);

      // description: ET :   //
      if (code === 'ET') setTelNumberDuplicationError(true);

      // description: DE :   //
      if (code === 'DE') alert(' .');
    }

    //          event handler          //
    // description:       //
    const onPasswordIconClickHandler = () => {
      setShowPassword(!showPassword);
    }
    // description:        //
    const onPasswordCheckIconClickHandler = () => {
      setShowPassowrdCheck(!showPasswordCheck);
    }
    // description:      //
    const onAddressIconClickHandler = () => {
      open({ onComplete });
    }
    // description:       //
    const onButtonClickHandler = () => {
      setEmailPatternError(false);
      setPasswordError(false);
      setPasswordCheckError(false);
      setNicknameError(false);
      setNicknameDuplicationError(false);
      setTelNumberError(false);
      setTelNumberDuplicationError(false);
      setAddressError(false);

      if (page === 1) checkPage1();
      if (page === 2) checkPage2();
    }
    // description:      //
    const onSignInClickHandler = () => {
      setView('sign-in');
    }
    // description:     //
    const onComplete = (data: Address) => {
      const address = data.address;
      setAddress(address);
    }

    //          component          //

    //          effect          //

    //          render          //
    return (
      <div className='auth-card'>
        <div className='auth-card-top'>
          <div className='auth-card-top-text-container'>
            <div className='auth-card-top-text'></div>
            <div className='auth-card-top-text-opacity'>{`${page}/2`}</div>
          </div>
          <div className='auth-card-top-input-container'>
            {page === 1 ? (
              <>
                <InputBox label=' *' type='text' placeholder='  .' error={emailPatternError || emailDuplicationError} helper={emailPatternError ? '   .' : emailDuplicationError ? '  .' : '' } value={email} setValue={setEmail} />
                <InputBox label='*' type={showPassword ? 'text' : 'password'} placeholder=' .' icon={showPassword ? INPUT_ICON.ON : INPUT_ICON.OFF} buttonHandler={onPasswordIconClickHandler} error={passwordError} helper={passwordError ? ' 8  .' : ''} value={password} setValue={setPassword} />
                <InputBox label=' *' type={showPasswordCheck ? 'text' : 'password'} placeholder='  .' icon={showPasswordCheck ? INPUT_ICON.ON : INPUT_ICON.OFF} buttonHandler={onPasswordCheckIconClickHandler} error={passwordCheckError} helper={passwordCheckError ? ' .' : ''} value={passwordCheck} setValue={setPasswordCheck} />
              </>
            ) : (
              <>
                <InputBox label='*' type='text' placeholder=' .' error={nicknameError || nicknameDuplicationError} helper={nicknameError ? ' .' : nicknameDuplicationError ? ' .' : ''} value={nickname} setValue={setNickname} />
                <InputBox label=' *' type='text' placeholder='  .' error={telNumberError || telNumberDuplicationError} helper={telNumberError ? ' .' : telNumberDuplicationError ? ' .' : ''} value={telNumber} setValue={setTelNumber} />
                <InputBox label='*' type='text' placeholder=' ' icon={INPUT_ICON.ARROW} error={addressError} helper={addressError ? ' .' : ''} value={address} setValue={setAddress} buttonHandler={onAddressIconClickHandler} />
                <InputBox label=' *' type='text' placeholder='  .' value={addressDetail} setValue={setAddressDetail} />
              </>
            )}
          </div>
        </div>
        <div className='auth-card-bottom'>
          <div className='auth-card-bottom-button' onClick={onButtonClickHandler}>
            { page === 1 ? ' ' : '' }
          </div>
          <div className='auth-card-bottom-text'>
              ? <span className='auth-emphasis' onClick={onSignInClickHandler}></span>
          </div>
        </div>
      </div>
    )
  }
  
  //          effect          //

  //          render          //
  return (
    <div id='auth-wrapper'>
      <div className='auth-left'>
        <div className='auth-left-icon'></div>
        <div className='auth-left-text-container'>
          <div className='auth-left-text'>.</div>
          <div className='auth-left-text'>Hoons Board .</div>
        </div>
      </div>
      <div className='auth-right'>
        {view === 'sign-in' ? (<SignInCard />) : (<SignUpCard />)}
      </div>
    </div>
  )
}
